<template>
    <div class="detail_list">
        <h2 class="headers">Detail</h2>
        <img :src="require(`@/assets/img/${detail.img}`)" alt="">
        <h4>{{detail.header}}</h4>
        <span>{{detail.paragr}}</span>
        <p>{{detail.detail}}</p>
    </div>
</template>
<style scoped>
   
    .detail_list {
        padding: 70px 40px 20px;
        text-align: center;
        margin: 0 auto;
    }
    .headers, h4, img {
        padding-bottom: 20px;
    }
    p {
        padding-top: 20px;
        margin: 0 auto;
        max-width: 800px;
        width: 100%;
    }
        
    img {
        width: auto;
    }

    @media screen and (max-width: 650px){
        img {
            width: 80%;
        }
    }
</style>

<script>
import axios from 'axios';
export default {
    name:'blog_detail',
    data(){
        return {
            id:this.$route.params.id,
            detail:{}
        }
    },
    created(){
        axios 
            .get('data/detail.json')
            .then((resp)=>{
                this.detail = resp.data.find((el)=> el.id == this.id)
            });

    }
}
</script>