<template>
    <div class="container">
        <h2 class="headers">Introducing Atolla custom routines</h2>
        <img :src="require(`@/assets/img/${product.img}`)" alt="">
        <h4>{{product.header}}</h4>
        <span>{{product.span}}</span>
        <p>{{product.detail}}</p>
    </div>
</template>
<style scoped>
    .container {
        padding: 70px 40px 20px;
        text-align: center;
        margin: 0 auto;
    }
    .headers, h4, img {
        padding-bottom: 20px;
    }
    p {
        padding-top: 20px;
        margin: 0 auto;
        max-width: 650px;
        width: 100%;
    }

</style>


<script>
import axios from 'axios';
export default ({
    name:"DetailProduct",

    data(){
        return {
            id:this.$route.params.id,
            product:{}
        }
    },
    created(){
        axios 
            .get('data/products.json')
            .then((resp)=>{
                this.product = resp.data.find((el)=> el.id == this.id)
            });

    }
    
})
</script>
