<template>
   
    <section class="products" id="products">
        <div class="container">
            <div class="products_header">
                <h2 class="headers">Introducing Atolla custom routines</h2>
            </div>
            <div class="products_wrap">
                <ul class="product_list">
                    <li class="products_item" v-for="product in products_list" :key="product.id">
                        <router-link :to="'/products/'+ product.id" rel="noopener nofollow" >
                            <img :src="require(`@/assets/img/${product.img}`)" alt="">
                            <h4>{{product.header}}</h4>
                            <span>{{product.span}}</span>
                            <p>{{product.paragr}}</p>
                        </router-link>
                    </li>
                </ul>
            </div>
        </div>
    </section>
    
</template>


<script>
import axios from 'axios'
export default ({
    name: 'Products',
    data(){
        return {
            products_list: [],
            
        }
    },
    created() {
        axios
            .get('data/products.json')
            .then((resp)=>{
                this.products_list = resp.data;
            })
    }

})
</script>
